import * as React from "react";
import * as Styles from "components/ShapesPalette.css";
import {connect} from "react-redux";
import {IAppState, IAction, IShapeAction} from "app";
import {Circle, Square, Triangle} from "components/Shapes";

interface IProps {
  selectedShape?: string;
}

interface IDispatch {
  dispatch(action: IAction): void;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShape: state.selectedShape,
  };
}

class ShapesPaletteBase extends React.Component<IProps & IDispatch> {
  public render(): JSX.Element {
    const shapes = [
      <Circle cx={50} cy={100} shapeKey="0" />,
      <Square cx={50} cy={200} shapeKey="1" />,
      <Triangle cx={50} cy={300} shapeKey="2" />,
    ];
    shapes.forEach(s => (s.props.isSelected = s.props.shapeKey === this.props.selectedShape));

    return (
      <svg className={Styles.palette} onClick={this.clickHandler}>
        {shapes}
      </svg>
    );
  }

  private clickHandler = (e: any) => {
    const key = e.target.attributes["data-shapekey"];
    this.props.dispatch({type: "SELECT_SHAPE", key: key ? key.value : undefined} as IShapeAction);
  };
}

export const ShapesPalette = connect(mapStateToProps)(ShapesPaletteBase);
